<template>
    <b-container>

         <b-row> <!-- text lead -->
            <b-col>
                <b-row>
                    <b-col><h1>MY PROFILE</h1></b-col>
                </b-row>
            </b-col>
        </b-row>

        <b-row class="spacer-headline"></b-row> <!-- divider -->

         <!-- my profile-->
        <b-row data-aos="fade-up" class="p-sm-1 p-3 mb-lg-5 mb-1"> <!-- text lead -->
            <b-col class="card-headline-no-border">
                <b-row>
                    <b-col class="introduction-body">
                        <b-row>
                            <b-col class="mt-2 mb-5 mb-md-1" cols="12" lg="6">
                                <img src="../assets/profile_img.jpg" class="profile-picture" />
                            </b-col>
                            <b-col class="mt-1" cols="12" lg="6">
                                
                                <b-col ><h1 class="border-bottom-line">PERSONAL INFO</h1></b-col>
                                <b-row data-aos="fade-right" data-aos-duration="500">
                                    <b-col cols="3">
                                        <p><strong>Name:</strong></p>
                                    </b-col>
                                    <b-col cols="9">
                                        <p>{{fullName}}</p>
                                    </b-col>
                                </b-row>
                                <b-row data-aos="fade-right" data-aos-duration="600">
                                    <b-col cols="3">
                                        <p><strong>Birthday:</strong></p>
                                    </b-col>
                                    <b-col cols="9">
                                        <p>{{birthDateStr}}</p>
                                    </b-col>
                                </b-row>
                                <b-row data-aos="fade-right" data-aos-duration="700">
                                    <b-col cols="3">
                                        <p><strong>Age:</strong></p>
                                    </b-col>
                                    <b-col cols="9">
                                        <p>{{age}} Years Old</p>
                                    </b-col>
                                </b-row>
                                <b-row data-aos="fade-right" data-aos-duration="800">
                                    <b-col cols="3">
                                        <p><strong>Gender:</strong></p>
                                    </b-col>
                                    <b-col cols="9">
                                        <p>{{gender}}</p>
                                    </b-col>
                                </b-row>
                                <b-row data-aos="fade-right"  data-aos-duration="900">
                                    <b-col cols="3">
                                        <p><strong>Languages:</strong></p>
                                    </b-col>
                                    <b-col cols="9">
                                        <p>{{knownLanguages}}</p>
                                    </b-col>
                                </b-row>
                            </b-col>
                        </b-row>
                    </b-col>
                </b-row>
            </b-col>
        </b-row>

        <!-- Educational Attainment -->
        <b-row class="spacer-headline"></b-row> <!-- divider -->

        <b-row>
            <b-col class="card-headline-no-border">
                <b-row>
                    <b-col><h1>EDUCATIONAL ATTAINMENT</h1></b-col>
                </b-row>
            </b-col>
        </b-row>

        <b-row class="p-sm-1 p-3 mt-5"> <!-- text lead -->
            <b-col>
               <b-row class="p-3 mt-1"  v-for="(e) in education" :key="e.level" data-aos="fade-up">
                    <b-col cols="12" class="mb-5">
                        <b-row>
                            <b-col cols="12" sm="5">
                                <img class="mt-sm-4" :src=getPic(e.imageUrl) style="border-radius:50%;max-width: 150px; height:auto" />
                            </b-col>
                            <b-col cols="12" sm="7" class="mt-4 mt-sm-1 text-center text-sm-left">
                                <p class="font-weight-bold" >{{capital(e.level)}}</p>
                                <p class="font-weight-bold h4">{{e.name}}</p>
                                <p>{{e.address}}</p>
                                <p style="margin-top: -15px;" v-html="e.details"></p>
                                <p style="margin-top: -15px;">{{e.yearAttended}}</p>
                            </b-col>
                        </b-row>
                    </b-col>
               </b-row>
            </b-col>
        </b-row>

    </b-container>
</template>

<script>
// @ is an alias to /src
export default {
  name: 'Myself',
  data(){
    return {
        'birthDate' : '10/08/1998',
        'age' : 0,
        'fullName' : 'Keith Andrew S. Pacio',
        'birthDateStr' : 'October 08, 1998',
        'gender' : 'Male',
        'religion' : 'Roman Catholic',
        'maritalStatus' : 'Single',
        'education' : [
            {
                'imageUrl' : 'assets/pup.png',
                'level' : 'college',
                'name' : 'Polytechnic University of the Philippines',
                'address' : 'Anonas, Sta. Mesa, Manila City',
                'details' : '<p class="font-weight-bold">Bachelor of Science in Computer Engineering</p>',
                'yearAttended' : '2014-2019'
            },
            {
                'imageUrl' : 'assets/ilis.png',
                'level' : 'high school',
                'name' : 'Isaac Lopez Integrated School',
                'address' : 'Ilino Cruz Street, Vergara, Mandaluyong City',
                'details' : '<p class="font-weight-bold">7th Honorable Mentioned, Best in Mathematics, Best in Science</p>',
                'yearAttended' : '2010-2014'
            },
        ],
        'languages':[
            "English",
            "Tagalog"
        ],
        'imageUrl' : "../assets/profile_img.jpg",
        'profileImage': {
          center: true,
          fluidGrow: true,
          blank: true,
          blankColor: '#bbb',
          width: 600,
          height: 400,
          class: 'my-5'
        }
    }
  },
  mounted(){
    this.getAge();   
  },
  methods:{
     getAge : function(){ //get current age
        var today = new Date();
        var birthDate = new Date(this.birthDate);
        var age = today.getFullYear() - birthDate.getFullYear();
        var m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
            age--;
        }
        this.age = age;
     }, 
     capital : function(str = ''){
        return str.toString().toUpperCase();
     },
     getPic (name) {
      return require("@/" + name);
     }

  },
  computed:{
    knownLanguages:function()
    {
        return this.languages.join(", ");
    } 
  },
  watch: {
      
  }
}
</script>

<style scoped>
.introduction-body p{
    line-height: 1.1;
}

.introduction-body{
    padding-top: 15px;
}

.visualizer-holder{
    margin-top:2%;
    padding:0!important;
    border: 5px solid var(--fColor);
}

.visualizer-img{
    /* margin-top : 2%; */
    object-fit: fill;
    width: 100%;
    /* border-radius: 30px; */
    /* border:5px solid var(--fColor); */
}

.visualizer-text{
    margin-top:2%;
}

h1{
    background: -webkit-linear-gradient(var(--fColor), var(--btnColor));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.card-margin{
    margin: 3% 0;
}

.card-headline .hyperlink, .hyperlink{
    display: block;
    text-decoration: none;
    background: -webkit-linear-gradient(var(--fColor), var(--btnColor));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.spacer-headline{
    margin : 5% 0%;
    border: 2px solid var(--fColor);
    border-radius:10px;
}

.card-headline{
    border: 2px solid var(--fColor);
    border-radius:10px;
    padding: 10px 10px;
    width: 70%;
}

.card-headline-no-border{
    padding: 10px 10px;
    width: 70%;
}

.card-headline .card-headline-img{
    object-fit: cover;
    width: 70%;
    min-width: 150px;
    height: auto;
    border-radius: 20px;
    border:1px solid var(--fColor);
    
}

.border-bottom-line{
    border-bottom: 2px solid var(--fColor);
}
.text-headline .row div p{
    text-align:center;
    display: inline;
    font-size : 45px; 
    background: -webkit-linear-gradient(var(--fColor), var(--btnColor));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: glow 2s ease-in-out infinite alternate;
}

.profile-picture{
    border-color: 100px solid var(--fColor); 
    border-radius: 50%;
    max-height:200px;
    width:auto;
}

@keyframes glow {
    from {
      text-shadow: 0 0 20px var(--btnColor);
    }
    to {
      text-shadow: 0 0 30px var(--btnColor), 0 0 10px var(--btnColor);
    }
}

</style>
